  <!--  -->
  <div class="container">
    <div class="test">
      <!--  -->
      <div class="superhero">
        <div class="hero" @click="unselect">
          <!--  -->
          <div class="head">
            <h1 class="title">Task Manager</h1>
          </div>
          <!--  -->
          <div class="ebox">
            <div v-if="!onLine" class="pop">
              <div class="msg">Connection Lost</div>
            </div>
            <div v-if="stop" class="stopbox" v-show="stop.length > 0">
              <li v-for="(error, index) in stop" :key="index">{{ error }}</li>
            </div>
          </div>
          <!--  -->
          <div class="io">
            <div class="create-post">
              <label for="create-post"></label>
              <input
                v-model="text"
                type="text"
                id="create-post"
                placeholder="Enter Task :"
                class="ip"
                v-on:keyup.enter="createPost"
              />
              <button class="btn" @click="createPost">Enter Task</button>
            </div>
            <p class="error" v-if="error">{{ error }}</p>
          </div>
          <div class="Img">
            <img src="../assets/logo.png" />
            <h3>Powered By Vue</h3>
          </div>
          <div class="footer">© 2022 . All rights reserved.</div>
        </div>
      </div>
      <!--  -->
      <!-- <hr class="divide" /> -->
      <div class="pcSuper">
        <div class="post-container">
          <!-- v-bind:index="index" -->
          <div
            class="post"
            v-for="post in posts"
            v-bind:item="post"
            v-bind:key="post._id"
          >
            <!-- v-on:dblclick="deletePost(post._id)" -->
            <div class="content">
              <input
                class="editbox"
                type="text"
                placeholder="Edit"
                v-model="edited"
                v-if="isSelected(post)"
                v-on:keyup.enter="updation(post._id, edited)"
              />
              <p
                v-else
                :class="[post.rem ? '' : 'text-s', 'text']"
                @click="post.rem = !post.rem"
              >
                {{ post.text }}
              </p>
            </div>
            <div class="options">
              <button class="Ubtn" @click="select(post)">Update</button>
              <button class="Dbtn" @click="deletePost(post._id)">Delete</button>
            </div>
          </div>
        </div>
      </div>
      <!-- <div class="foot">Taskify Goals !</div> -->
    </div>
  </div>
</template>

<script>
import PostService from '../PostService'
// import JwPagination from 'jw-vue-pagination'
// import Spin_C from '../components/Spin_C.vue'

export default {
  name: 'PostComponent',
  inject: ['spinstate'],
  data() {
    return {
      posts: [],
      stop: [],
      error: '',
      text: '',
      edited: '',
      onLine: navigator.onLine ? true : false,
      toggle: false,
      flag: false,
      editor: false,
      selected: {},
    }
  },
  async created() {
    console.log(this.edited)
    try {
      this.spinstate.value = true
      this.posts = await PostService.getPosts()
      // console.log(this.posts)
      this.spinstate.value = false
    } catch (err) {
      this.error = err.message
    }
    setInterval(() => {
      this.checkOnline()
    }, 30000)
  },
  methods: {
    async updation(id, edited) {
      if (!edited) {
        // console.log('expty string')
        this.stop.push('Empty Task Invalid')
      } else {
        this.flag = !this.flag
        await PostService.updatePost(id, this.edited)
        this.posts = await PostService.getPosts()
        this.edited = ''
        this.unselect()
      }
    },
    async createPost() {
      this.stop = []
      if (!this.text) {
        // console.log('expty string')
        this.stop.push('Empty Task Invalid')
      } else {
        this.spinstate.value = true
        await PostService.insertPost(this.text)
        this.text = ''
        this.posts = await PostService.getPosts()
        console.log(this.stop)
        this.spinstate.value = false
      }
    },
    async deletePost(id) {
      this.spinstate.value = true
      await PostService.deletePost(id)
      this.posts = await PostService.getPosts()
      this.spinstate.value = false
    },
    checkOnline() {
      this.onLine = navigator.onLine ? true : false
      if (this.onLine) {
        this.stop = []
      } else {
        this.stop.push('System Offline !')
      }
    },
    select(item) {
      this.selected = item
      this.edited = item.text
      console.log(this.selected)
    },
    unselect() {
      this.selected = {}
      this.editedDescription = ''
    },
    isSelected(item) {
      return item._id === this.selected._id
    },
  },

  // components: { 'jw-pagination': JwPagination },
  // Spin_C
}
</script>

